body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #553055;
  word-wrap: break-word;  
  white-space: pre-wrap;
}
.App{
  background: #4c2a4c;
  margin: 20px auto;
  width: 90%;
  max-width: 700px;
  color: #eee;
   
  
}
.navbar{
  padding: 10px 20px;
  text-align: center;
  background: #6d3d6d;
  box-shadow: 1px 1px 10px 3px  rgb(54, 7, 40);
  border-top-left-radius: 1em;
  border-top-right-radius: 1em;
  overflow: hidden;
}
.navbar h1{
  margin: 10px 0;
}
.book-list{
  margin: 10px;
  
}
.book-list ul{
  padding: 0;
  list-style-type: none;
  margin-top: 65px;
 

}
.book-list li{
  background: #6d3d6d;
  border-radius: 4px;
  padding: 10px;
  margin: 10px 0;
  overflow: hidden;
  box-shadow: 1px 1px 10px 3px  rgb(54, 7, 40);
 
  

}
.book-list li.done{
  background: #3d1234;
}
 

.book-list .title{
  font-weight: bold;
  color: #fff;
  font-size: 1.2em;
}
.book-list .author{
  font-size: 0.9em;
  color: #ddd;
}
.book-list .title.done{
 color: #666
}
.book-list .author.done{
  color: #666;
}


.empty{
  margin: 20px;
  text-align: center;
}

form{
  padding:  0px;
 
}
input[type='text'], input[type='password'], textarea{
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  margin: 6px 0;
  background: #3c1f3c;
  color: #fff;
  border: 0;
  border-radius: 4px;
  box-shadow: 1px 1px 5px 3px  rgb(54, 7, 40);
}

textarea{
  font-family: "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  font-size: 1.2em;
}


input[type='submit']{
  margin: 5px auto;
  background: #eee;
  border: 0;
  padding: 6px 20px;
  display: block;
  border-radius: 4px;
  box-shadow: 1px 1px 10px 3px  rgb(54, 7, 40);
  float: right;
}

.btn{
  margin: 10px auto;
  background: #eee;
  border: 0;
  padding: 6px 20px;
  display: block;
  border-radius: 4px;
  box-shadow: 1px 1px 10px 3px  rgb(54, 7, 40);
 /* float: right; */
  display: inline-block;
   cursor: pointer;

}
.btn:hover{
  background: #aaa;
 
}

.btn:disabled{
  background: #666;
  color: #111;
}



.deleteBtn {
/*margin-top: 5px;*/
  background: #eee;
  border: 0;
  float: right;
  cursor: pointer;
  border-radius: 4px;
  box-shadow: 1px 1px 10px 3px  rgb(54, 7, 40);
  /* padding: 6px 20px; */
   /* display: block;  */
}
